<template>
  <div class="agreement">
    <h2>{{ agreement.text }}</h2>
    <div v-for="(item, index) in agreement.subtags" :key="index">
      <h3>{{ item.text }}</h3>
      <p>{{ item.subtags }}</p>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  props: {
    agreement: {
      type: Object,
      default: {},
      required: true
    }
  },
  data () {
    return {
    }
  },
  computed: {
    ...mapState({
      english: states => states.english
    })
  },
  mounted () {

  },
  components: {
  }
}
</script>

<style lang="scss" scoped>
.agreement {
  box-sizing: border-box;
  padding: 10px;
}

</style>



