<script>
import router from '@/router'
import { mapState } from 'vuex'
export default {
  components: {
  },
  data () {
    return {
      selected: 'home'
    }
  },
  mounted () {
    let _this = this
    router.afterEach((to, from, next) => {
      let curP = to.path.substring(1)
      if (curP === 'home' || curP === 'market' || curP === 'mountain' || curP === 'mine') {
        _this.selected = to.path.substring(1)
      }
    })
  },
  computed: {
    ...mapState({
      english: states => states.english
    })
  },
  watch: {
    selected: function (newVal, oldVal) {
      switch (this.selected) {
        case 'home':
          // go to page home
          this.$router.push({meta: {auth: true}, path: '/home'})
          break
        case 'market':
          // go to page market
          this.$router.push({meta: {auth: true}, path: '/market'})
          break
        case 'mountain':
          // go to page mountain
          this.$router.push({meta: {auth: true}, path: '/mountain'})
          break
        case 'mine':
          // go to page mine
          this.$router.push({meta: {auth: true}, path: '/mine'})
          break
        default:
          // go to error
      }

      this.$emit('getSelected', this.selected)
    }
  }
}
</script>
<template>
  <mt-tabbar v-model="selected" :fixed="true">
    <mt-tab-item id="home" class="page-logo">
      <i class="page-img iconfont icon-panda" slot="icon"></i>
      <span v-if="!english"></span><span v-else>Home</span>
    </mt-tab-item>
    <mt-tab-item id="market" class="page-logo">
      <i class="page-img iconfont icon-tiaosaoshichang" slot="icon"></i>
      <span v-if="!english"></span><span v-else>page 1</span>
    </mt-tab-item>
    <mt-tab-item id="mountain" class="page-logo">
      <i class="page-img iconfont icon-faxian" slot="icon"></i>
      <span v-if="!english"></span><span v-else>page 2</span>
    </mt-tab-item>
    <mt-tab-item id="mine" class="page-logo">
      <i class="page-img iconfont icon-wode" slot="icon"></i>
      <span v-if="!english"></span><span v-else>Mine</span>
    </mt-tab-item>
  </mt-tabbar>
</template>

<style lang="scss" scoped>
</style>




