<template>
  <div>
    <div class="head-title">
      <h1 class="marketcontent-title clearfix">
        <span class="fl">
          <h1 v-if="!english"></h1>
          <h1 v-else>Market</h1>
        </span>
      </h1>
    </div>
    <search-bar></search-bar>
    <div
      v-infiniteScroll="getPets"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10">
      <pet-list 
        :data="pets" 
        :title="petsTitle">
      </pet-list>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import PetList from '../components/petList'
import { InfiniteScroll } from 'mint-ui'
import SearchBar from '../components/searchBar'
export default {
  data () {
    return {
      searchVal: '',
      petsTitle: '',
      pets: [],
      total: 0,
      page: 0,
      perpage: 1,
      loading: false
    }
  },
  computed: {
    ...mapState({
      english: states => states.english
    })
  },
  mounted () {
    if (this.english) {
      this.petsTitle = 'Market Pets'
    }
    else {
      this.petsTitle = ''
    }
    // this.getPets()
  },
  methods: {
    getPets () {
      let _this = this
      this.$store.dispatch('getPets', {
        params: {
          page: _this.page,
          perpage: _this.perpage,
          order: 'asc',
          sort: 'years'
        },
        cb: function (data) {
          console.log(data)
          _this.pets = _this.pets.concat(data.data)
          _this.total = data.total
          if ((_this.page + 1) * _this.perpage >= data.total) {
            _this.loading = true
          }
        }
      })
    }
  },
  components: {
    PetList,
    InfiniteScroll,
    SearchBar
  }
}
</script>

<style lang="scss" scoped>
.marketcontent-title {
  padding: 0 8px 0 20px;
}
</style>



