<template>
  <div class="panel">
    <div class="sb clearfix">
      <search
        class="sb-search fl"
        v-model="value"
        placeholder=""
        background="#fff"
        @search="onSearch"
      />
      <div class="sb-order fr">
        <ul>
          <li 
            class="sb-orderItem iconfont" 
            :class="{'icon-xiangshang': isup && currentIndex === index, 'icon-xiangxia': !isup && currentIndex === index, 'active': currentIndex === index}"
            @click="orderFn(index)"
            v-for="(item, index) in orderArr" 
            :key="index">
            {{ item.name }}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { Search } from 'vant'
export default {
  data () {
    return {
      value: '',
      orderArr: [{name: '', isup: true}, {name: '', isup: true}, {name: '', isup: true}],
      isup: true,
      currentIndex: 0,
      page: 0,
      perpage: 30,
      order: 'asc',
      sort: 'years'
    }
  },
  methods: {
    onSearch () {
      console.log(this.value)
      let _this = this
      this.$store.dispatch('getPets', {
        params: {
          page: _this.page,
          perpage: _this.perpage,
          order: _this.order,
          sort: _this.years,
          petId: '',
          memberId: '',
          status: 0
        },
        cb: function (data) {
          _this.pets = _this.pets.concat(data.data)
          _this.total = data.total
          if ((_this.page + 1) * _this.perpage >= _this.total) {
            _this.loading = true
          }
        }
      })
    },
    onCancel () {
      console.log(this.value)
    },
    orderFn (index) {
      this.isup = !this.isup
      this.currentIndex = index
    }
  },
  components: {
    Search
  }
}
</script>

<style lang="scss" scoped>
.sb {
  width: 100%;
  .sb-search,
  .sb-order {
    width: 50%;
    height: 40px;
  }
  .sb-search {
    padding: 4px 6px;
  }
  .sb-order {
    text-align: right;
  }
  .sb-orderItem {
    display: inline-block;
    margin: 0 10px;
    width: 80px;
  }
}
</style>




