<template>
  <div>
    <div class="head-title">
      <h1 class="minecontent-title clearfix">
        <span class="fl">
          <h1 v-if="!english"></h1>
          <h1 v-else>Personal Center</h1>
        </span>
        <div class="fr head-right-icon">
          <mt-button 
            class="fr minecontent-lang" 
            @click.native="changeLanguage">
            <span class="iconfont" :class="{'icon-yingwenyuyan': english, 'icon-zhongwenyuyan': !english}"></span>
          </mt-button>
        </div>
      </h1>
    </div>
    <div class="minecontent-block panel">
      <mt-cell :title="user.nickName">
        <img slot="icon" class="avatar" src="../assets/img/avatar.png" width="24" height="24">
        <span @click="popupVisible = true">  &nbsp; > </span>
      </mt-cell>
    </div>
    <div class="minecontent-block panel">
      <div v-for="(item, index) in mineArr" :key="index">
        <mt-cell @click.native="openPop(item, index)">
          <span v-if="!english">{{ item.text }}</span><span v-else>{{ item.textEn }}</span>
          <span slot="icon" class="iconfont" :class="item.icon"></span>
        </mt-cell>
      </div>
    </div>
    <mt-popup v-model="popupVisible" position="bottom" class="mine-personBtn tac">
      <mt-button 
        class="mine-personBtn-item" 
        @click.native="openConfirm">
        <span v-if="!english"></span>
        <span v-else>Log out</span>
        </mt-button>
      <mt-button 
        class="mine-personBtn-item" 
        @click.native="popupVisible = false">
        <span v-if="!english"></span>
        <span v-else>Cancel</span>
        </mt-button>
    </mt-popup>

    <mt-popup v-model="popShow" popup-transition="popup-fade" class="mine-detailInfo">
      <mt-cell @click.native="closePop(item, index)" :title="!english ? '' : 'Back'"></mt-cell>
      <div v-show="currentIndex === 0">
        <page-personal></page-personal>
      </div>
      <div v-show="currentIndex === 1">
        <page-pet-info></page-pet-info>
      </div>
      <div v-show="currentIndex === 2">
        <page-help></page-help>
      </div>
      <div v-show="currentIndex === 3">
        <page-tell></page-tell>
      </div>
      <div v-show="currentIndex === 4">
        <page-recharge></page-recharge>
      </div>
      <div v-show="currentIndex === 5">
        <page-invite></page-invite>
      </div>
    </mt-popup>

  </div>
</template>

<script>
import { mapState } from 'vuex'
import { MessageBox } from 'mint-ui'
import PagePersonal from '@/components/pagePersonal'
import PagePetInfo from '@/components/pagePetInfo'
import PageHelp from '@/components/pageHelp'
import PageTell from '@/components/pageTell'
import PageRecharge from '@/components/pageRecharge'
import PageInvite from '@/components/pageInvite'
export default {
  data () {
    return {
      popupVisible: false,
      popupPersonal: false,
      popupPetInfo: false,
      popupHelp: false,
      popupTell: false,
      popupRecharge: false,
      popupInvite: false,
      currentIndex: -1,
      popShow: false,
      mineArr: [
        {
          text: '',
          textEn: 'Personal information',
          variable: 'popupPersonal',
          icon: 'icon-gerenxinxi'
        },
        {
          text: '',
          textEn: 'Pet information',
          variable: 'popupPetInfo',
          icon: 'icon-chongwu'
        },
        {
          text: '',
          textEn: 'Help Center',
          variable: 'popupHelp',
          icon: 'icon-bangzhuzhongxin'
        },
        {
          text: '',
          textEn: 'Customer service phone',
          variable: 'popupTell',
          icon: 'icon-icon-service'
        },
        {
          text: '',
          textEn: 'Recharge',
          variable: 'popupRecharge',
          icon: 'icon-chongzhi'
        },
        {
          text: '',
          textEn: 'Invite Friends',
          variable: 'popupInvite',
          icon: 'icon-yaoqinghaoyou1'
        }
      ]
    }
  },
  computed: {
    ...mapState({
      english: states => states.english,
      user: state => state.user
    })
  },
  watch: {
    currentIndex () {
      this.currentIndex === -1 ? this.popShow = false : this.popShow = true
    }
  },
  methods: {
    openConfirm () {
      MessageBox.confirm('?', '').then(action => {
        this.$router.push('/')
      }).catch(action => {
      })
    },
    changeLanguage () {
      this.$store.commit('ENGLISH', !this.english)
    },
    openPop (item, index) {
      this.currentIndex = index
    },
    closePop (item, index) {
      this.currentIndex = -1
    }
  },
  components: {
    PagePersonal,
    PagePetInfo,
    PageHelp,
    PageTell,
    PageRecharge,
    PageInvite
  }
}
</script>

<style lang="scss" scoped>
  .minecontent-title {
    padding: 0 8px 0 20px;
  }
  .minecontent-block {
    margin: 8px;
    .avatar {
      width: 100px;
      height: 100px;
      border-radius: 50px;
      background-color: #ededed;
    }
  }
  .mine-personBtn {
    width: 100%;
    .mine-personBtn-item {
      display: block;
      width: 100%;
    }
  }
  .mine-detailInfo {
    width: 100%;
    height: 100%;
    background-color: #fff;
  }
</style>


