<template>
  <div>
    <homehead></homehead>
    <search-bar></search-bar>
    <div
      v-infiniteScroll="getPets" 
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10">
      <pet-list
        class="homecon-petlist" 
        :data="pets" 
        :title="petsTitle">
      </pet-list>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Homehead from '../components/homehead'
import PetList from '../components/petList'
import SearchBar from '../components/searchBar'
export default {
  name: 'homecontent',
  data () {
    return {
      petsTitle: '',
      pets: [],
      total: 0,
      page: 0,
      perpage: 30,
      order: 'asc',
      sort: 'years',
      loading: false
    }
  },
  mounted () {
    if (this.english) {
      this.petsTitle = 'My Pets'
    }
    else {
      this.petsTitle = ''
    }
    // this.getPets()
  },
  computed: {
    ...mapState({
      english: states => states.english,
      user: states => states.user
    })
  },
  methods: {
    getPets () {
      let _this = this
      this.$store.dispatch('getPets', {
        params: {
          page: _this.page,
          perpage: _this.perpage,
          order: _this.order,
          sort: _this.years,
          memberId: _this.user.memberId
        },
        cb: function (data) {
          _this.pets = _this.pets.concat(data.data)
          _this.total = data.total
          if ((_this.page + 1) * _this.perpage >= _this.total) {
            _this.loading = true
          }
        }
      })
    },
    loadmore () {
      if ((this.page + 1) * this.perpage < this.total) {
        this.page++
        this.getPets()
      }
    }
  },
  components: {
    Homehead,
    PetList,
    SearchBar
  }
}
</script>

<style lang="scss" scoped>
.homecon-petlist {
  overflow-y: auto;
}
</style>



