<script>
import { mapState } from 'vuex'
import footerbar from '../components/footerbar'
import homecontent from '../components/homecontent'
import marketcontent from '../components/marketcontent'
import mountaincontent from '../components/mountaincontent'
import minecontent from '../components/minecontent'
export default {
  name: 'Home',
  data () {
    return {
      selected: 'home'
    }
  },
  computed: {
    ...mapState({
      english: states => states.english
    })
  },
  methods: {
    getSelected (val) {
      this.selected = val
      this.$router.push({val})
    }
  },
  components: {
    footerbar,
    homecontent,
    marketcontent,
    mountaincontent,
    minecontent
  }
}
</script>

<template>
  <div class="home">
    <div class="home-wrap">
      <mt-tab-container class="page-tabbar-container" v-model="selected">
        <mt-tab-container-item id="home">
          <homecontent></homecontent>
        </mt-tab-container-item>
        <mt-tab-container-item id="market">
          <marketcontent></marketcontent>
        </mt-tab-container-item>
        <mt-tab-container-item id="mountain">
          <mountaincontent></mountaincontent>
        </mt-tab-container-item>
        <mt-tab-container-item id="mine">
          <minecontent></minecontent>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>

    <footerbar @getSelected="getSelected"></footerbar>
  </div>
</template>

<style lang="scss" scoped>
.home {
  height: 100vh;
  overflow: hidden;
}
.home-wrap {
  height: 100%;
  overflow: auto;
  padding-bottom: 60px;
  .page-tabbar-container {
    height: 100%;
    #home,
    #market,
    #mountain,
    #mine {
      height: 100%;
    }
  }
}
</style>


