<template>
  <div>
    <div class="page-recharge">
      <div>
        <span v-if="!english"></span><span v-else>Recharge</span>
      </div>
      <div>
        <span><span v-if="!english"></span><span v-else>Recharge your two-dimensional code below</span></span>
        <img src="" alt="">
        <mt-cell @click.native="popupRecharge = false;"><span v-if="!english"></span><span v-else>Determine</span></mt-cell>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {

    }
  },
  computed: {
    ...mapState({
      english: states => states.english
    })
  }
}
</script>

<style lang="scss" scoped>
.page-recharge {
  margin-top: 50%;
  font-size: 20px;
  text-align: center;
  line-height: 2;
}
</style>




